import { useState, useEffect } from 'react';
import {
    Box,
    Grid,
    Paper,
    Typography,
    List,
    ListItem,
    ListItemIcon,
    ListItemButton,
    Divider,
    Button,
    FormControl,
    InputLabel,
    OutlinedInput

} from '@mui/material';

import {
    Delete, Edit
} from '@mui/icons-material';

import PageWrapper from 'src/layout/page-wrapper'
import { blue, red } from '@mui/material/colors';

import { axiosInstance } from 'src/axiosApi';


export default function TodoPage() {

    const [ formOpen, setFormOpen ] = useState(false)
    const [ formData, setFormData ] = useState({title: ''})
    const [ items, setItems ]: any = useState([])
    const [ editedItem, setEditedItem ]: any = useState({id: 0, title: ''})

    const getItems = () => {
        axiosInstance
          .get(`/api/todos/`)
          .then( res => {
            setItems( res.data )
          })
          .catch( err => console.log(err.request) )
    };

    useEffect( () => {
        getItems()
    }, [])

    /**
     * Allows selected item to be edited by swapping \<Typography> with \<OulinedInput>
     */
    const enableItemEdit = (e) => {
        setEditedItem({id: e.target.id, title: e.target.value})
        setItems( items.map( (item, index: number) => (
            parseInt(item.id) === parseInt(e.target.id) ?
                {
                    ...item,
                    editable: item.editable ? !item.editable : true
                }
            :
                {
                    ...item,
                    editable: false
                }
        )))
    };

    const postEditedItem = () => {
        axiosInstance
            .put(`/api/todos/${editedItem.id}/`, {title: editedItem.title} )
            .then( res => {
                getItems()
                // Change back to uneditable after submitting change
                setItems( items.map( (item) => (
                        {...item, editable: false}
                )))
                console.log(res.data)
            })
            .catch( err => console.log(err.data) )
    };

    const createItem = () => {
        axiosInstance
            .post(`/api/todos/`, formData)
            .then( res => {
                setFormData({title: ''})
                setFormOpen(false)
                getItems()
                console.log(res.data)
            })
            .catch( err => console.log(err) )
    }

    const deleteItem = (e) => {
        axiosInstance
            .delete(`/api/todos/${e.target.id}`)
            .then( res => {
                getItems()
                console.log(res.data)
            })
            .catch( err => console.log(err))
    };

    return (
        <PageWrapper styling={{p: 3, display: 'flex', alignItems: 'center', justifyContent: 'center'}}>
            <Paper elevation={12}>
                <Grid
                    container
                    sx={{
                        height: '20rem',
                        width: '25rem',
                        overflowY: 'auto',
                    }}
                >
                    <Grid 
                        item xs={12}
                        sx={{
                            height: '15%',
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'space-between',
                            px: 2
                        }}
                    >
                        <Typography
                            variant='h5'
                            sx={{textAlign: 'center', alignSelf: 'center',}}
                        >
                            Todo's
                        </Typography>
                        <Button
                            variant='contained'
                            onClick={ getItems }
                            sx={{justifySelf: 'start',}}
                            color='success'
                        >
                            Refresh List
                        </Button>
                    </Grid>
                        
                    <Grid
                        item xs={12}
                        sx={{
                            mb:3
                        }}
                    >
                        <Button
                            variant='contained'
                            size='small'
                            sx={{
                                ml: 2,
                                bgcolor: formOpen ? red[600] : blue[600],
                                '&:hover': {
                                    bgcolor: formOpen ? red[800] : blue[800],
                                }
                            }}
                            onClick={ () => setFormOpen(!formOpen) }
                        >
                            { formOpen ? '- Close Form' : '+ Add New Item' }
                        </Button>
                    </Grid>

                    <Grid
                        item xs={12}
                        sx={{
                            px: formOpen ? 3 : 0,
                            py: formOpen ? 2 : 0,
                            height: formOpen ? 'auto' : 0,
                            overflow: 'hidden'
                        }}
                    >
                        <FormControl fullWidth>
                            <InputLabel shrink htmlFor="item-title">Title</InputLabel>
                            <OutlinedInput
                                id="item-title"
                                aria-describedby="item-title"
                                label='Title'
                                notched
                                value={ formData.title ? formData.title : '' }
                                onChange={ e => setFormData({...formData, title: e.target.value}) }
                            />
                        </FormControl>
                        <Button
                            variant='contained'
                            fullWidth
                            sx={{mt: 2}}
                            onClick={ createItem }
                        >
                            Submit
                        </Button>
                    </Grid>
                    { formOpen && 
                        <Grid>

                        </Grid>
                    }
                    <Grid item container xs={12} sx={{height: '85%',}}>
                        <List sx={{width: '100%',}}>
                            { items.length > 0 ? 
                                items.map( (item, index: number) => (
                                <div key={ index }>
                                    <Divider variant='middle'/>
                                    <ListItem
                                        sx={{
                                            display: 'flex',
                                            justifyContent: 'space-between',
                                            alignItems: 'center',
                                        }}
                                    >
                                        { item.editable ? 
                                            <>
                                                <FormControl fullWidth sx={{mx: 2, my: 1}}>
                                                    <InputLabel shrink htmlFor="item-title">Title</InputLabel>
                                                    <OutlinedInput
                                                        id="item-title"
                                                        aria-describedby="item-title"
                                                        label='Title'
                                                        notched
                                                        value={ editedItem.title ? editedItem.title : item.title }
                                                        onChange={ e => setEditedItem({...editedItem, title: e.target.value}) }
                                                        sx={{height: '2.5rem'}}
                                                    />
                                                </FormControl>
                                                <Button
                                                    variant='contained'
                                                    sx={{py: 1, px: 3, mr: 1, height: '2.5rem'}}
                                                    onClick={ postEditedItem }
                                                >
                                                    Submit
                                                </Button>
                                            </>
                                        :
                                            <Typography>
                                                { item.title }
                                            </Typography>
                                        }

                                        {/* Update and Delete Icons Below */}
                                        <Box sx={{display: 'flex'}}>
                                            <ListItemButton
                                                sx={{
                                                    width: '2.5rem',
                                                    height: '2.5rem',
                                                    justifyContent: 'center',
                                                    mr: 1
                                                }}
                                                id={item.id}
                                                onClick={ enableItemEdit }
                                            >
                                                <ListItemIcon
                                                    sx={{
                                                        color: blue[600],
                                                        justifyContent: 'center',
                                                        pointerEvents: 'none',
                                                    }}
                                                >
                                                    <Edit sx={{pointerEvents: 'none'}}/>
                                                </ListItemIcon>
                                            </ListItemButton>
                                            
                                            <ListItemButton
                                                sx={{
                                                    width: '2.5rem',
                                                    height: '2.5rem',
                                                    // border: '1px solid black',
                                                    justifyContent: 'center'
                                                }}
                                                id={item.id}
                                                onClick={ deleteItem }
                                            >
                                                <ListItemIcon
                                                    sx={{
                                                        color: red[600],
                                                        justifyContent: 'center',
                                                        pointerEvents: 'none',
                                                        // border: '1px solid black',
                                                    }}
                                                >
                                                    <Delete sx={{pointerEvents: 'none'}}/>
                                                </ListItemIcon>
                                            </ListItemButton>
                                        </Box>
                                    </ListItem>
                                </div>
                            ))
                            :
                                <Typography sx={{width: '100%', textAlign: 'center'}}>
                                    No Items Created
                                </Typography>
                            }
                        </List>
                    </Grid>

                    
                </Grid>
            </Paper>
        </PageWrapper>
    );
}