import {
  // List,
  // ListItem,
  // Typography,
  Button,
  Grid,
  // Box
} from '@mui/material'
import { useState } from 'react';
// import axios from 'axios';
import { axiosInstance } from 'src/axiosApi';
import PageWrapper from 'src/layout/page-wrapper'

interface User {
  email?: string;
  username?: string;
}

export default function UsersPage() {

  const [ users, setUsers ]: any = useState([])


  const getUsers = () => {
    axiosInstance
      .get('/auth/users/')
      .then( res => {
        // console.log(res.data)
        setUsers( res.data )
      })
      .catch( err => console.log(err) )
    };

  return (
    <PageWrapper styling={{p: 3}}>
        <Button variant='contained' onClick={ getUsers }>Get Updated List</Button>
        <Grid container spacing={2} sx={{width: '100%', display: 'flex', flexDirection: 'column'}}>
          { users ? 
            users.map( (user: User, index: number) => (
              <Grid
                container
                key={index}
                spacing={1}
                sx={{mt: 2}}
              >
                <Grid item xs={2} sx={{textAlign: 'right'}}>Email: </Grid>
                <Grid item xs={10} sx={{}}>{user.email}</Grid>
                <Grid item xs={2} sx={{textAlign: 'right'}}>Username: </Grid>
                <Grid item xs={10} sx={{}}>{user.username}</Grid>
              </Grid>
            ))
          :
            null
          }
        </Grid>
    </PageWrapper>
  );
}
