import {
    Typography,
    // Skeleton,
    // Grid,
    Box,
    Paper,

    useMediaQuery
} from '@mui/material';
import PageWrapper from 'src/layout/page-wrapper'


export default function BlogDetailPage() {

    const laptop = useMediaQuery('(min-width:1200px)')
    const tablet = useMediaQuery('(min-width:900px)')

    const blog_detail = {
        title: 'Automate Stuff With Python',
        date: '19 December 2021',
        content: 'Lorem ipsum dolor sit amet consectetur, adipisicing elit. Veritatis laboriosam provident nostrum accusamus velit vel esse maiores veniam non? Corrupti ex praesentium vitae molestiae ipsam explicabo, consequatur dicta? Id fugiat, unde enim, assumenda aperiam, nobis natus perferendis officiis eligendi libero recusandae harum accusamus voluptas odio impedit dolore necessitatibus ducimus quas quisquam nisi non asperiores atque! Harum dolorem corrupti, sunt molestiae atque autem repudiandae nostrum quisquam eveniet neque aut vitae odit vel eaque? Harum sed soluta ipsum perspiciatis animi excepturi deleniti magni commodi facere quibusdam, laboriosam accusamus unde enim ut. Quos illum laborum laboriosam nisi quas ipsa dignissimos, incidunt libero ullam?',
    }

    return (
        <PageWrapper
            styling={{
                // display: 'flex',
                // justifyContent: 'center',
                // alignItems: 'center',
                // p: 10
            }}
        >
            <Paper
                elevation={12}
                sx={{
                    bgcolor: 'primary.dark',
                    minWidth: '18rem',
                    maxWidth: '40rem',
                    // m: 3,
                    mt: 15,
                    mx: laptop ? 'auto' : (tablet ? 10 : 3)
                }}
            >
                <Box
                    sx={{
                        p: 2
                    }}
                >
                    <Typography
                        variant='caption'
                    >
                        {blog_detail.date}
                    </Typography>

                    <Typography
                        variant='h5'
                        sx={{
                            mt: 1
                        }}
                    >
                        {blog_detail.title}
                    </Typography>

                    <Typography
                        variant='body2'
                        sx={{
                            mt: 5
                        }}
                    >
                        {blog_detail.content}
                    </Typography>

                </Box>
            </Paper>
        </PageWrapper>
    );
}