import {
    Typography,
    // Skeleton,
    Grid,
    // Box,
    Button,
    Paper,

    useMediaQuery,
} from '@mui/material';
import PageWrapper from 'src/layout/page-wrapper'
import { NavLink } from 'react-router-dom';


export default function ProjectsPage() {

    const laptop = useMediaQuery('(min-width:1200px)');

    const recent_projects = [
        {title: 'Coming Soon'},
        {title: 'Coming Soon'},
    ]

    return (
        <PageWrapper 
            styling={{
                display: laptop ? 'flex' : 'block', 
                alignItems: 'center', 
                justifyContent: 'center',
            }}
        >
            <Grid
                container
                rowSpacing={8}
                columnSpacing={4}
                sx={{p: 3}}
            >

                <Grid
                    item
                    xs={12}
                    sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}
                >
                    <Typography sx={{fontSize: 30}}>
                        My Recent Projects
                    </Typography>
                </Grid>

                { recent_projects.map( ({title}, index: number) => (
                    <Grid
                        key={index}
                        item
                        xs={12}
                        lg={6}
                        sx={{
                            display: 'flex',
                            justifyContent: laptop ? (index === 0 ? 'flex-end' : 'flex-start') : 'center',
                            alignItems: 'center',
                            // border: '1px solid black'
                        }}
                    >
                        <Paper
                            elevation={12}
                            sx={{
                                bgcolor: 'primary.dark',
                                minWidth: '18rem',
                                width: '22rem',
                                maxWidth: '22rem',
                                height: '11rem',
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                fontSize: 25
                            }}
                        >
                            {title}
                        </Paper>
                    </Grid>
                ))}

                <Grid
                    item
                    xs={12}
                    sx={{display: 'flex', justifyContent: 'center', alignItems: 'center'}}
                >
                    <NavLink
                        to={'projects/all'}
                        style={{textDecoration: 'none'}}
                    >
                        <Button
                            variant='contained'
                            sx={{
                                textTransform: 'none',
                                fontWeight: 'fontWeightRegular',
                                color: 'text.primary',
                                bgcolor: 'primary.dark',
                                '&:hover': {
                                    bgcolor: 'primary.main'
                                }
                            }}
                        >
                            All Projects &gt;
                        </Button>
                    </NavLink>
                </Grid>
            </Grid>
        </PageWrapper>
    );
}