import {
    // Grid,
    // Paper,
    Box,
    Typography,

    useMediaQuery,
} from '@mui/material';
import PageWrapper from 'src/layout/page-wrapper'
// import { NavLink } from 'react-router-dom';
import Masonry from '@mui/lab/Masonry';


export default function AllProjectsPage() {

    const laptop = useMediaQuery('(min-width:1200px)');
    const tablet = useMediaQuery('(min-width:900px)');
    // const mobile = useMediaQuery('(min-width:600px)');
    const custom1 = useMediaQuery('(min-width:680px)');

    const recent_projects = [
        {title: 'Coming Soon', image: 200},
        {title: 'Coming Soon', image: 240},
        {title: 'Coming Soon', image: 300},
        {title: 'Coming Soon', image: 220},
        {title: 'Coming Soon', image: 210},
        {title: 'Coming Soon', image: 280},
        {title: 'Coming Soon', image: 310},
        {title: 'Coming Soon', image: 280},
        {title: 'Coming Soon', image: 280},
        {title: 'Coming Soon', image: 290},
        {title: 'Coming Soon', image: 350},
    ]

    return (
        <PageWrapper 
            styling={{
                display: 'flex',
                // flexDirection: 'column',
                // alignItems: 'center',
                justifyContent: 'center'
            }}
        
        >
            <Box
                sx={{
                    width: laptop ? 1000 : (tablet ? 800 : (custom1 ? 700 : 400)),
                }}
            >
                <Masonry
                    columns={{ xs: 1, lg: 2}}
                    spacing={4}
                    sx={{
                        m: 0,
                    }}
                >
                    {recent_projects.map(({title, image}, index) => (
                    <Box
                        key={index} 
                        sx={{
                            height: image,
                            minWidth: 300,
                            maxWidth: 300,
                            bgcolor: '#333',
                            // textAlign: 'center',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center'
                        }}
                    >
                        <Typography>
                            {title}
                        </Typography>
                    </Box>
                    ))}
                </Masonry>
            </Box>
            {/* <Grid
                container
                rowSpacing={6}
                columnSpacing={6}
                sx={{my: 5, p: 0}}
            >
                { recent_projects.map( ({title}, index: number) => (
                    <Grid
                        key={index}
                        item
                        xs={12}
                        lg={6}
                        sx={{
                            display: 'flex',
                            justifyContent: laptop ? ((index % 2) === 0 ? 'flex-end' : 'flex-start') : 'center',
                            alignItems: 'center'
                        }}
                    >
                        <Paper
                            elevation={12}
                            sx={{
                                width: '22rem',
                                height: '11rem',
                                bgcolor: 'primary.dark',
                                display: 'flex',
                                justifyContent: 'center',
                                alignItems: 'center',
                                textTransform: 'none',
                                fontWeight: 'fontWeightRegular'
                            }}
                        >
                            {title}
                        </Paper>
                    </Grid>
                ))}

            </Grid> */}
        </PageWrapper>
    );
}