import React, { useRef } from 'react';
import { Editor } from '@tinymce/tinymce-react';
import {
    Grid,
    Button,

    // useMediaQuery
    Theme, useTheme
} from '@mui/material';
import PageWrapper from 'src/layout/page-wrapper'


export default function BlogDetailEdit() {

    const theme: Theme = useTheme();

    const editorRef = useRef(null);
    
    const log = () => {
        if (editorRef.current) {
            // @ts-ignore: Object is possibly 'null'.
          console.log(editorRef.current.getContent());
        }
      };

    // const laptop = useMediaQuery('(min-width:1200px)')
    // const tablet = useMediaQuery('(min-width:900px)')


    return (
        <PageWrapper
            styling={{
            }}
        >
            <Grid
                container
                sx={{
                    pt: 10,
                    px: 20
                }}
            >
                <Grid
                    item
                    xs={12}
                >
                    <Editor
                        // @ts-ignore: Type 'Editor' is not assignable to type 'null'.
                        onInit={(evt, editor) => editorRef.current = editor}
                        initialValue="<p>This is the initial content of the editor.</p>"
                        init={{
                            skin: theme.palette.mode === 'dark' ? "oxide-dark" : '',   // Doesn't work ?
                            content_css: theme.palette.mode === 'dark' ? "dark" : '',  // Also doesn't work ?
                            height: 500,
                            width: 'auto',
                            // menubar: false,
                            menubar: 'favs file edit view insert format tools table help',
                            plugins: [
                                'advlist autolink lists link image charmap print preview anchor',
                                'searchreplace visualblocks code fullscreen',
                                'insertdatetime media table paste code help wordcount'
                            ],
                            toolbar: 'preview code undo redo | formatselect | ' +
                                'bold italic backcolor | alignleft aligncenter ' +
                                'alignright alignjustify | bullist numlist outdent indent | ' +
                                'removeformat | help',
                            content_style: 'body { font-family:Helvetica,Arial,sans-serif; font-size:14px }'
                        }}
                    />
                </Grid>
                <Grid
                    item
                    xs={12}
                    sx={{
                        textAlign: 'center',
                        mt: 5
                    }}
                >
                    <Button
                        onClick={log}
                        sx={{
                            textTransform: 'none',
                            fontWeight: 'fontWeightRegular',
                            color: 'text.primary',
                            bgcolor: 'primary.dark',
                            '&:hover': {
                                bgcolor: 'primary.main'
                            },
                            width: '10rem'
                        }}
                    >
                            Submit Post
                    </Button>
                </Grid>
            </Grid>
        </PageWrapper>
    );
}