import {
    Typography,
    Box,
    Grid,

    useMediaQuery,
} from '@mui/material';
import PageWrapper from 'src/layout/page-wrapper'
import { NavLink } from 'react-router-dom';


export default function AllBlogsPage() {

    const tablet = useMediaQuery('(min-width:900px)');
    const mobile = useMediaQuery('(min-width:600px)');

    const posts = [
        {
            title: 'Building your first Django / React project',
            date: '02 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'Automating stuff with Python',
            date: '15 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'Material UI Introduction',
            date: '28 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'React Introduction',
            date: '07 January 2022',
            image: '',
            link: ''
        },
        {
            title: 'Material UI Introduction',
            date: '28 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'React Introduction',
            date: '07 January 2022',
            image: '',
            link: ''
        },
        {
            title: 'Material UI Introduction',
            date: '28 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'React Introduction',
            date: '07 January 2022',
            image: '',
            link: ''
        },
        {
            title: 'Material UI Introduction',
            date: '28 December 2021',
            image: '',
            link: ''
        },
        {
            title: 'React Introduction',
            date: '07 January 2022',
            image: '',
            link: ''
        },
    ]

    return (
        <PageWrapper
            styling={{
                display: mobile ? 'flex' : 'block',
                justifyContent: 'center',
                alignItems: 'center'
            }}
        >

            <Grid
                container
                columnSpacing={4}
                rowSpacing={2}
                sx={{
                    // border: '1px solid black',
                    m: 'auto',
                    p: 4
                }}
            >
                <Grid
                    item
                    xs={12}
                    sx={{textAlign: 'center', mb: 2}}
                >
                    <Typography
                        variant='h4'
                    >
                        All Posts
                    </Typography>
                </Grid>
                { posts && posts.map( ({title, date, image, link}, index: number) => (
                    <Grid
                        key={index}
                        item
                        xs={12}
                        sm={6}
                        lg={4}
                        sx={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: tablet 
                                                ? 'center'
                                                :   (mobile
                                                        ? (index%2 === 0 ? 'flex-end' : 'flex-start')
                                                        : 'center'
                                                    ),
                            p: 3
                        }}
                    >
                        <NavLink
                            to={link}
                            style={{
                                textDecoration: 'none',
                                color: 'inherit'
                            }}
                        >
                            <Box
                                sx={{
                                    minWidth: '10rem',
                                    width: '10rem',
                                    maxWidth: '10rem',
                                    
                                    minHeight: '10rem',
                                    height: '10rem',
                                    maxHeight: '10rem',
                                    
                                    boxShadow: '2px 2px 8px 0px #111',
                                    p: 1,
                                }}
                            >
                                <Typography
                                    variant='caption'
                                    sx={{
                                        height: '15%'
                                    }}
                                >
                                    {date}
                                </Typography>

                                <Typography
                                    sx={{
                                        width: '100%',
                                        height: '85%',
                                        display: 'flex',
                                        alignItems: 'center',
                                        justifyContent: 'center',
                                        textAlign: 'center'
                                    }}
                                >
                                    {title}
                                </Typography>
                            </Box>
                        </NavLink>
                    </Grid>
                ))
                }
            </Grid>

        </PageWrapper>
    );
}